<template>
  <div class="alphabet">
    <div class="sort" v-for="item in letters" :key="item"
      :ref='item'
      @click="changAlphabet"
      @touchstart="touchstart"
      @touchmove='touchmove'
      @touchend='touchend'
    >
    {{ item }}</div>
  </div>
</template>

<script>
import { clearTimeout, setTimeout } from 'timers'
export default {
  name: 'alphabet',
  props: {
    cities: Object
  },
  data: function () {
    return {
      touchstatus: false,
      startY: 0,
      timer: null
    }
  },
  updated () {
    this.startY = this.$refs['A'][0].offsetTop
  },
  methods: {
    changAlphabet (e) {
      this.$emit('changeAlphabet', e.target.innerText)
    },
    touchstart () {
      this.touchstatus = true
    },
    touchmove (e) {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        if (this.touchstatus) {
          var touchY = e.touches[0].clientY - 84
          var index = Math.floor((touchY - this.startY) / 20)
          if (index >= 0 && index < this.letters.length) {
            this.$emit('changeAlphabet', this.letters[index])
          }
        }
      }, 16)
    },
    touchend () {
      this.touchstatus = false
    }
  },
  computed: {
    // eslint-disable-next-line vue/return-in-computed-property
    letters () {
      var letters = []
      // eslint-disable-next-line no-unused-vars
      for (let i in this.cities) {
        letters.push(i)
      }
      return letters
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~scss/default';

  .alphabet{
    position: fixed;
    top: 1.68rem;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .sort{
      font-size: .24rem;
      padding: .08rem;
      text-align: center;
      color:$bg-color;
    }
  }
</style>
