<template>
  <div class="list" ref="list">
    <div>
       <div class="now">
        <div class="title border-topbottom">当前城市</div>
        <div class="city-list">
          <div class="city-wrapper">
            <div class="city">{{ this.$store.state.city }}</div>
          </div>
        </div>
      </div>
      <div class="hot border-topbottom">
        <div class="title">热门城市</div>
        <div class="city-list">
          <div class="city-wrapper" v-for="item in hot" :key="item.id"
            @click="changeCity(item.name)"
          >
            <div class="city">{{ item.name }}</div>
          </div>
        </div>
      </div>
      <div class="sort" v-for="(item,key) in cities" :key="key"  :ref="key">
        <div class="title border-topbottom">{{ key }}</div>
        <div class='sort-city border-bottom'
        v-for="city in item" :key="city.id"
        @click="changeCity(city.name)"
        >{{ city.name }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
  name: 'list',
  props: {
    hot: Array,
    cities: Object,
    letter: String
  },
  data: function () {
    return {}
  },
  created () {
    // eslint-disable-next-line no-undef
    this.$nextTick(() => {
      this._initBetterScroll()
    })
  },
  methods: {
    _initBetterScroll () {
      // eslint-disable-next-line no-new
      this.scroll = new BScroll(this.$refs['list'], {
        click: true
      })
    },
    changeCity (city) {
      this.$store.commit('changeCity', city)
      this.$router.push('/')
    }
  },
  watch: {
    letter () {
      if (this.letter) {
        this.scroll.scrollToElement(this.$refs[this.letter][0])
      }
    }
  }
}
</script>

<style lang='scss' scoped>

/*公共样式*/
.title {
  padding: 0.24rem 0.3rem;
  background-color: #eee;
  font-size: 0.24rem;
}
.border-topbottom{
  &::before , &::after{
    border-color: #999;
  }
}
.border-bottom{
  &::before{
    border-color: #999;
  }
}
.city-list{
  box-sizing: border-box;
  width: 100%;
  box-sizing: border-box;
  padding: .1rem .5rem .1rem .1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .city-wrapper{
    box-sizing: border-box;
    width: 33.3%;
    padding: .1rem;
    .city{
      box-sizing: border-box;
      border: .02rem solid #ccc;
      text-align: center;
      border-radius: .06rem;
      padding: .08rem 0;
    }
  }
}
/*公共样式*/

.list{
  position: absolute;
  top: 1.68rem;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: 0;
  .now {
    width: 100%;
  }
  .sort{
    width: 100%;
    .sort-city{
      line-height: .80rem;
      padding-left: .2rem;
    }
  }
}

</style>
