import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home/Home.vue'
import City from './views/City/City.vue'
import Detail from './views/Detail/Detail.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      name: 'default',
      path: '/',
      redirect: '/home'
    },
    {
      name: 'home',
      path: '/home',
      component: Home
    },
    {
      name: 'city',
      path: '/city',
      component: City
    },
    {
      name: 'detail',
      path: '/detail/:id',
      component: Detail
    }
  ]
})
