<template>
    <div>
        <Header/>
        <Search :cities='cities'/>
        <List :hot='hot' :cities='cities' :letter='letter'/>
        <Alphabet :cities='cities' @changeAlphabet='changeAlphabet'/>
    </div>
</template>

<script>
import Header from './components/Header'
import Search from './components/Search'
import List from './components/List'
import Alphabet from './components/Alphabet'
import axios from 'axios'

export default {
  name: 'city',
  components: {
    Header,
    Search,
    List,
    Alphabet
  },
  data: function () {
    return {
      hot: [],
      cities: {},
      letter: ''
    }
  },
  methods: {
    changeAlphabet (letter) {
      this.letter = letter
    }
  },
  mounted () {
    axios.get('/api/city')
      .then(res => {
        var data = res.data.data
        if (res.data.ret) {
          this.hot = data.hotCities
          this.cities = data.cities
        }
      })
  }
}
</script>

