<template>
  <div>
    <Header ref="header" />
    <sideAnimation>
      <Slide :gallaryImgs="gallaryImgs"  @toClose="close" v-show="isBanner"/>
    </sideAnimation>
    <div class="list" ref="list">
      <div>
        <Banner :gallaryImgs="gallaryImgs" :sightName="sightName" :bannerImg="bannerImg" @toOpen='toOpen' />
        <List :list="list"/>
      </div>
    </div>
  </div>
</template>

<script>
import Banner from './components/Banner'
import Header from './components/Header'
import Slide from './components/Slide'
import List from './components/List'
import sideAnimation from '@/assets/common/side/sideAnimation'

import BScroll from 'better-scroll'
import axios from 'axios'

export default {
  name: 'detail',
  components: {
    Banner,
    Header,
    Slide,
    List,
    sideAnimation
  },
  data: function () {
    return {
      isBanner: false,
      scrollY: 0,
      sightName: '',
      bannerImg: '',
      gallaryImgs: [],
      list: []
    }
  },
  mounted () {
    axios.get('/api/detail', {
      params: {
        id: this.$route.params.id
      }
    })
      .then(res => {
        res = res.data
        if (res.ret) {
          this.sightName = res.data.sightName
          this.bannerImg = res.data.bannerImg
          this.gallaryImgs = res.data.gallaryImgs
          this.list = res.data.categoryList
        }
      })

    this.scroll = new BScroll(this.$refs['list'], {
      click: true,
      probeType: 3
    })
    this.scroll.on('scroll', pos => {
      this.scrollY = -1 * (Math.round(pos.y))
    })
  },
  methods: {
    toOpen () {
      this.isBanner = true
    },
    close () {
      this.isBanner = false
    }
  },
  watch: {
    scrollY () {
      this.$refs.header.changeHeader(this.scrollY)
    }
  }
}
</script>

<style lang='scss' scoped>
.list {
  z-index: -1;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  .content {
    width: 100%;
    height: 50rem;
  }
}
</style>
