<template>
  <div >
    <swiper class="banner" :options="swiperOption" @click="close">
      <swiper-slide class="img-wrapper" v-for="(item,index) in gallaryImgs" :key="index">
        <img width="100%" :src="item" alt />
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'slide',
  props: {
    gallaryImgs: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          type: 'fraction'
        },
        observer: true,
        observeParents: true
      }
    }
  },
  methods: {
    close () {
      this.$emit('toClose')
    }
  }
}
</script>

<style lang='scss' scoped>
  .banner{
    z-index: 99;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #000;
    .img-wrapper{
      display: flex;
      align-items: center;
    }
    .swiper-pagination{
      z-index: 999;
      color:#fff;
      position: absolute;
      bottom: .7rem;
    }
  }
</style>
