<template>
  <div class="swiper">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(src,index) in swiperList" :key="index">
        <img width="100%" :src="src.imgUrl">
        </swiper-slide>

      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'swiper-page',
  props: {
    swiperList: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          type: 'bullets'
        },
        loop: true,
        autoplay: {
          delay: 1500,
          stopOnLastSlide: false,
          /* 触摸滑动后是否继续轮播 */
          disableOnInteraction: false
        }
      }
    }
  },
  methods: {}
}
</script>

<style lang='scss' scoped>
  @import '~scss/default';
  .swiper{
    overflow: hidden;
    width: 100%;
    height: 0;
    padding-bottom: 26.6%;
    background-color: #eee;

  }
</style>
<style scoped>
.swiper >>>.swiper-pagination-bullet-active{
  background-color: rgba(0,175,190,.8) !important;
}
.swiper >>>.swiper-pagination-bullet{
   width: .12rem;
   height: .12rem;
}
</style>
