<template>
  <div class="header">
    <div class="header-star" v-show="change" >
      <span :style="top" class="back iconfont" @click="back">&#xe611;</span>
    </div>
    <div class="header-change" v-show="!change" :style="changeStyle">
      <span class="iconfont" @click="back">&#xe611;</span>
      <div class="title">城市选择</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home-header',
  data: function () {
    return {
      change: true,
      changeStyle: {
        opacity: 0
      },
      top: {
      }
    }
  },
  methods: {
    back () {
      this.$router.back()
    },
    changeHeader (top) {
      console.log(top)
      if (top > 60) {
        var opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.changeStyle = { opacity }
        this.change = false
      } else {
        this.change = true
      }
      if (top < 0) {
        this.top = {
          top: -top + 5 + 'px'
        }
      }
    }
  }
}
</script>

<style lang='scss' scoped>
@import "~scss/default.scss";

.header {
  z-index: 0;
  .header-star {
    .back {
      position: absolute;
      top: 0.1rem;
      left: 0.1rem;
      background-color: rgba(0, 0, 0, 0.5);
      padding: 0.15rem;
      border-radius: 50%;
      font-size: 0.38rem;
      font-weight: bold;
      color: rgba(255, 255, 255, 0.9);
    }
  }
  .header-change {
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: row;
    background-color: $bg-color;
    padding-right: 0.8rem;
    &.move-enter-active,&.move-leave-active{
      transition: all 2s linear
    }
    &.move-enter,&.move-leave-to{
      opacity: 0;

    }
    .iconfont {
      width: 0.8rem;
      height: 0.88rem;
      text-align: center;
      line-height: 0.88rem;
      color: #fff;
      font-size: 0.36rem;
    }
    .title {
      width: 100%;
      text-align: center;
      line-height: 0.88rem;
      font-size: 0.32rem;
      color: #fff;
    }
  }
}
</style>
