<template>
  <div>
    <div class="search">
      <input class="search-input" type="text" placeholder="输入城市名或拼音" v-model="keywords" />
    </div>
    <div class="search-content" v-show="keywords" ref='search'>
      <ul>
        <li class="search-item border-bottom"
          v-for="(item,index) in list" :key="index"
          @click="changeCity(item.name)"
        >
          {{ item.name }}
        </li>
        <li class="search-item" v-show="!list.length">
          "没有搜索到匹配对象"
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { clearTimeout, setTimeout } from 'timers'
import BScroll from 'better-scroll'

export default {
  name: 'search',
  props: {
    cities: Object
  },
  data () {
    return {
      keywords: '',
      list: [],
      timer: null
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.search, {
      click: true
    })
  },
  methods: {
    changeCity (city) {
      this.$store.commit('changeCity', city)
      this.$router.push('/')
    }
  },
  watch: {
    keywords () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keywords) {
        this.list = []
      }
      this.timer = setTimeout(() => {
        const list = []
        for (let i in this.cities) {
          this.cities[i].forEach(res => {
            if (res.name.indexOf(this.keywords) > -1 ||
              res.spell.indexOf(this.keywords) > -1
            ) {
              list.push(res)
            }
          })
        }
        this.list = list
      }, 100)
    }
  }
}
</script>

<style lang='scss' scoped>
@import "~scss/default.scss";

.search {
  z-index: 10;
  background-color: $bg-color;
  display: flex;
  justify-content: center;
  padding-bottom: 0.2rem;
  .search-input {
    box-sizing: border-box;
    width: 95%;
    height: 0.6rem;
    text-align: center;
    border-radius: 0.06rem;
    color: #666;
    padding: 0 0.1rem;
  }
}
.search-content{
  z-index: 1;
  overflow: hidden;
  position: absolute;
  top: 1.68rem;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #eee;
  .search-item{
    line-height: .6rem;
    padding-left: .2rem;
    background-color: #fff;
    color: #666;
  }
  .border-bottom{
    &::before{
      border-color: #eee;
    }
  }
}
</style>
