<template>
  <div>
    <swiper class="iconlist border-bottom" :options="swiperOption">
      <swiper-slide v-for="(page,index) in pages" :key="index">
        <div class="wrapper">
          <div class="item" v-for="(item,index) in page" :key="index">
            <img class="itemImg" :src="item.imgUrl" alt="">
            <p class="p">{{item.desc}}</p>
          </div>
        </div>
      </swiper-slide>

      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <div class="bottom">
      <div class="b-left border-right">
        <span class="iconfont">&#xe61f;</span>附近
      </div>
      <div class="b-right">
        <span class="iconfont">&#xe76c;</span>必游榜单
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'iconlist',
  props: {
    iconList: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    }
  },
  data: function () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          type: 'bullets'
        },
        loop: true
      }
    }
  },
  computed: {
    // eslint-disable-next-line vue/return-in-computed-property
    pages () {
      // eslint-disable-next-line no-unused-vars
      let pageArray = []
      this.iconList.forEach((item, index) => {
        // eslint-disable-next-line no-unused-vars
        let page = Math.floor(index / 8)
        if (!pageArray[page]) {
          pageArray[page] = []
        }
        pageArray[page].push(item)
      })
      return pageArray
    }
  },
  methods: {

  }
}
</script>

<style lang='scss' scoped>
  .iconlist{
    overflow: hidden;
    width: 100%;
    height: 0;
    padding-top: .1rem;
    padding-bottom: 50%;
    .wrapper{
      width: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      .item{
        display: flex;
        flex-direction: column;
        width: 25%;
        padding-top: .1rem;
        justify-content: center;
        align-items: center;
        .itemImg{
          width: 1.1rem;
          height: 1.1rem;
        }
        .p{
          width: 100%;
          padding-top: .1rem;
          font-size: .28rem;
          text-align: center;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
      }
    }
  }
  .bottom{
    width: 100%;
    height: 1rem;
    display: flex;
    flex-direction: row;
    font-size: .28rem;
    .b-left{
      width: 50%;
      text-align: center;
      line-height: 1rem;
      .iconfont{
        margin-right: .05rem;
      }
    }
    .b-right{
      width: 50%;
      text-align: center;
      line-height: 1rem;
      .iconfont{
        margin-right: .05rem;
      }
    }
  }
</style>
<style scoped>
.iconlist >>>.swiper-pagination-bullet-active{
  background-color: rgba(0,175,190,.8) !important;
}
.iconlist >>>.swiper-pagination-bullet{
   width: .12rem;
   height: .12rem;
}
</style>
